body{
  font-family: 'Montserrat', sans-serif;
  padding: 0;
  margin: 0;
}

.nav{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  /*box-shadow: gray 0px 0px 7px;*/


}
.nav-title{
  font-size: 1rem;
  font-weight: 1000;
  margin: 1rem 1rem 1rem 2rem;

}
.nav-links{
  margin-right: 2rem;
    display: flex;
    flex-direction: row;
    align-items: center;

}

.nav-link{
  text-decoration: none;
  color: black;
  margin: 1rem;
  font-size: 1rem;
  font-weight: 300;
  transition: all 0.3s ease-in-out;
}
.nav-link:hover{
  color: #FFC107;
}
.app {

    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    justify-items: center;
    margin-bottom: 1rem;

}

.app_img {
    display: flex;
    flex-direction: column;
    width: 40%;

}

.content {
    display: flex;
    flex-direction: column;
    justify-items: center;
    justify-content: flex-start;
    margin-top: 5rem;
    margin-left: 1rem;
    width: 60%;
}
.description{
    font-size: 1rem;
    font-weight: 300;
    text-align: center;


}
.description h3{
    font-size: 1.7rem;
    font-weight: 300;
    text-align: center;
}
.description p{
    font-size: 1rem;
    font-weight: 300;
    text-align: center;
}
.links{
    margin-top: 3rem;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;


}
.about-log{
    text-align: center;

}
.about{
    display: flex;
    /*flex-direction: row;*/
    flex-direction: row-reverse;
    text-align: center;
    margin:auto;
    width: 70%;
}
.about-img{
    width: 400px;
    height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
}
.b{
    display: flex;
    flex-direction: row;
    width: 90%;
    margin: auto;
    margin-top: 3rem;
    justify-content: space-between;
}

.card{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
    /*background-color: #f8f1f8;*/
    background-color: #da1c6b;
    /*background-color: #f8de93;*/
    width: 30%;
    padding-bottom: 2rem;
    padding-top: 1rem;
    margin-bottom: 3rem;
    margin-left: 0.5rem;
    margin-right: 0.5rem;

}
.card-btn{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    padding: 0.8rem 5rem;
    margin-top: 2rem;
    margin-bottom: 0.5rem;
    border: none;
    background-color: white;
    box-shadow: grey 0px 0px 7px;
    text-decoration: none;
    color: black;
    transition: all 0.3s ease-in-out;
    font-family: 'Montserrat', sans-serif;
    font-weight: bolder;
}
.card-btn:hover{
    background-color: #ed9019;
}
.card-text{
    color: white;
}
.card-list{
    display: flex;
    flex-direction: row;
    color: white;
    justify-content: center;
    align-items: center;

}

.signup-title{
    font-size: 1.7rem;
    font-weight: 300;
    text-align: center;
    margin-top: 0.5rem;
    margin-bottom: 3rem;
    color:white;
}


.signup-form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 2rem auto auto;
    width: 55%;
    background-color: #da1c6b;
    border-radius: 20px;
    padding-bottom: 5rem;
    padding-top: 3rem;
    margin-bottom: 3rem;
}

.signup-row{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 50%;
    margin-bottom: 1rem;
}
.signup-row label{
    font-size: 1rem;
    font-weight: 300;
    margin-bottom: 0.5rem;
    color: white;
}
.signup-row input{
    border-radius: 8px;
    border: none;
    padding: 0.5rem 1rem;
    margin-bottom: 1rem;
    font-size: 1rem;
    font-weight: 300;
    color: black;
}
.signup-btn{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    padding: 0.8rem 3rem;
    margin-top: 2rem;
    margin-bottom: 0.5rem;
    border: none;
    background-color: white;
    box-shadow: grey 0px 0px 7px;
    text-decoration: none;
    color: black;
    transition: all 0.3s ease-in-out;
    font-family: 'Montserrat', sans-serif;
    font-weight: bolder;
}
.signup-btn:hover{
    background-color: #ed9019;
}
.signup-link{
    margin-top: 2rem;
    color: white;
    font-size: small;
}

.footer{
    background-color: #282d32;
    /*position: fixed;*/
    bottom: 0;
    width: 100%;
    /*height: 150px;*/
} /* Height of the footer */

.footer-text{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    color: gray;
    font-size: 0.7rem;
    margin-top: 0.5rem;
}
.footer-link{
    display: flex;
    flex-direction: row;
    padding-left: 5rem;
    text-decoration: none;
    margin: 0.8rem;
    font-weight: 300;
    color:#f0f9ff;
    font-size:0.8em;
    transition:0.5s;

}
.footer-link:hover{
    color: #FFC107;
}
.footer-links{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    color: gray;
    padding-right: 4rem;
    /*margin-top: 1rem;*/

}
.logo-txt{
    font-size: 1rem;
    font-weight: 10;
    /*margin-top: 2.5rem;*/
    /*margin-left: 3rem;*/
    color: white;
}

.footer-connect{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    width: 30%;
    margin: auto;
    color: gray;
    font-size: 0.7rem;
    padding-bottom: 0.5rem;
    margin-top: 1rem;
}

